.speech{
    margin: 5% 0%;
    font-family: 'Gentium Plus', serif;
    padding: 2% 25%;
    background-color:#B9B4A8;
    display:flex;
    flex-wrap: wrap;  
    justify-content:center;
    .icon-container{
        color: #0C0C22;
        font-size: 30px;
        margin: 5% 0%;
    }
    .container-speech{
        width: 100%;
        font-size: 30px;
        text-align: center;
        color:#0C0C22;
        font-weight: 300;
    }
    .container-author{
        margin-top: 5%;
        color: #343468;
        width: 100%;
        display: flex;
        justify-content: center;
    }
}
@media (max-width: 900px) {
    .speech{
        padding: 5% 5%;
        .container-speech{
            font-size: 20px;
        }
    }
}